@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;800;900&family=Poppins:wght@300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span,
p,
ul {
  margin: 0;
  padding: 0;
}
:focus{
  box-shadow: none !important;
  outline: none !important;
}
.main-page-heading{
  font-weight: 600;
}
.loginbg{
    background: #000055;
}

.custom-form-main{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.custom-form{
    margin: 0 auto;
    background: #fff;
    border: 1px solid;
    padding: 50px;
    border-radius: 20px;
    width: 40%;
}

.main-heading{
    text-transform: capitalize;
    text-align: center;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 30px;
}

.custom-button{
    background: #000055;
    border: 1px solid #000055;
    padding: 7px 35px;
    width: -webkit-max-content;
    width: max-content;
    display: block;
    margin: 0 auto;
    transition: 0.5s all ease;
}
.custom-button:hover{
    background: transparent;
    color: #000055;
}

@media (max-width: 1024px){
    .custom-form{
        width: 100%;
    }
}
.box-link{
    text-decoration: none;
    color: #000;
    text-align: center;
}

.main-box {
    box-shadow: 0px 0px 5px 2px #eee;
    padding: 25px;
    border-radius: 20px;
    transition: 0.5s all ease;
    height: 230px;
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
}

.main-box .box-heading h5 {
    font-size: 22px;
    font-weight: 600;
}

.main-box .box-icon {
    width: 110px;
    margin: 15px auto;
}
.main-box .box-icon h5{
    font-size: 22px;
    font-weight: 600;
}
.main-box:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}
